.button {
    display: inline-block;
    cursor: pointer;
    border: 0;
    border-radius: 5px;
    font-weight: 700;
    line-height: 1;
    font-family: "Nunito Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.button--primary {
    background-color: #555ab9;
    color: white;
}
.button--secondary {
    box-shadow: rgba(0, 0, 0, 0.15) 0px 0px 0px 1px inset;
    background-color: transparent;
    color: #333;
}
.button--small {
    padding: 10px 16px;
    font-size: 12px;
}
.button--medium {
    padding: 11px 20px;
    font-size: 14px;
}
.button--large {
    padding: 12px 24px;
    font-size: 16px;
}
